<template>
    <div class="user-cash-back">
        <div class="row mb-xs">
            <div class="mb-xs total-points values">   <display-value            type="number" label="total points"        :value="user.fidelity.earned" /> </div>
            <div class="cashback-earned  values"    >       <display-value prefix="€" type="money"  label="cashback earned"     :value="user.cashback.earned" /></div>
        </div>
        <div class="row mt-xs">
            <div class="mb-xs total-awards values" >  <display-value            type="number" label="total awards"        :value="user.fidelity.retired" /> </div>
            <div class="cashback-retired values"  >    <display-value prefix="€" type="money"  label="cashback retired"    :value="user.cashback.retired" /></div>
        </div>
    </div>
</template>

<script>
    import allFilters from '../../../../../assets/filters'
    import {mapState} from 'vuex'

    export default{
        computed:{
            ...mapState(['debugMode']),
            cols(){
                return 'cols col-xs-12 col-sm-6 col-md-6 col-lg-6 '
            }
        },
        components: {
            DisplayValue: {
                template: `<div class="display-value input-group wd-wide" >
                             <div class="form-control input-xs label" type="text">{{$t(label)}}</div>
                             <span v-if="type=='number'" class="input-group-addon input-xs valuta-width value">{{prefix}} {{value | integer}} {{suffix}}</span>
                             <span v-if="type=='money'" class="input-group-addon input-xs valuta-width value">{{prefix}} {{value | money}} {{suffix}}</span>
                          </div>`,
                filters: {...allFilters},
                props: ['label', 'value', 'prefix', 'suffix', 'type']
            }
        },
        props: {
            user:Object
        }
    }
</script>


<style lang="scss" scoped>

</style>


