<template>
    <div class="be-user-list">
        <div class="align-left mb">
            <pagination class="pull-right"
                for="portal_user"
                :records="records"
                :per-page="perPage"
                :chunk="chunk"
            ></pagination>

            <span class="text-bold text-sm">{{ $t('found users',{count: records  },records) }}:</span>
        </div>

        <div v-for="(user,index) in userList">
            <div class="panel b user-panel align-left bt2">

                <div class="panel-body"> <user-item  :user="user" ></user-item>  </div>

                <div class="panel-footer user-item-panel-footer" v-if="user.userName=='Andre'">

                    <div class="col-xs-4">
                        <div class="panel widget bg-primary user-rep-button">
                            <div class="row row-table ">
                                <div class="col-xs-2 text-center bg-primary-dark user-rep-button-icon-el">
                                    <em class="icon-cloud-upload"></em>
                                </div>
                                <div class="col-xs-8 user-rep-button-body-el">
                                    <div class="user-rep-button-label">{{ $t('you have request cashback')}}</div>
                                    <div class="user-rep-button-title">€ 50,00</div>
                                </div>
                                <div class="col-xs-2 text-center bg-primary-dark user-rep-button-icon-el">
                                    <img src="images/cash_receiving32x32.png" alt="">
                                </div>
                            </div>
                        </div>
                    </div>

                </div>

            </div>
        </div>

        <pagination class="text-right"
            for="portal_user"
            :records="records"
            :per-page="perPage"
            :chunk="chunk"
        ></pagination>

    </div>
</template>
<script>
    import Vue from 'vue'
    import {mapState,mapActions} from 'vuex'
    import SearchFilter from './SearchFilter'
    import {VuePagination} from 'vue-pagination-2';
    import allFilters from '../../../../../assets/filters'
    import UserItem from './UserItem'
    import ResizeSensor from 'vue-resize-sensor'

    export default{
        filters:{...allFilters},
        watch:{
            page(val){
                this.loadUsers();
            }
        },
        methods:{
            ...mapActions('portal_user',[ 'loadUsers' ])
        },
        computed:{
            ...mapState('portal_user',[ 'userList', 'page', 'records', 'perPage' ,'chunk'])
        },
        created(){
            if( this.userList.length==0 )
                this.loadUsers();
        },
        components:{ UserItem }
    }


</script>

<style lang="scss">
    $user-panel-body-height : 56px;
    $user-item-panel-footer-height: 50px;
    $user-rep-button-height: ($user-panel-body-height / 2);


    .user-item-panel-footer{
        height:$user-item-panel-footer-height;
    }
    .user-rep-button{
        height:$user-rep-button-height !important;
        border-radius:0 ;
        border:0;
        .user-rep-button-label{
            font-size: 9px;
            font-weight:normal ;
        }
        .user-rep-button-title{
            font-size: 11px;
            font-weight:normal ;
        }
        .user-rep-button-icon-el,user-rep-button-body-el{
            height:$user-rep-button-height;
        }
    }

    .avatar-data{
        font-size:11px;
    }
    .sep{
        min-height:$user-panel-body-height;
        border-right:1px solid rgba(0, 0, 0, 0.12);
        width:2px;
    }
    .user-panel .panel-body{
        min-height: $user-panel-body-height;
        padding: 15px 15px 6px 15px;
    }

    .pagination {
        margin: 0px  !important;
        border-radius: 4px;
    }

</style>


