<template>
    <div class="user-item">
        <div v-if="false" class="debug-el"></div>
        <div class="row" >
            <div class="mb br user-avatar-container">
               <user-avatar :user="user" />
            </div>
            <div class="br mb-sm user-cash-back-container" >
                <user-cash-back :user="user"  />
            </div>
            <div class="reservation-recap-container">
                <reservation-recap :user="user" />
            </div>
        </div>

    </div>
</template>

<script>
    import allFilters from '../../../../../assets/filters'
    import UserCashBack from './UserCashBack'
    import {mapState} from 'vuex'

    export default {
        components:{
            UserAvatar:{
                template:`
                            <div class="user-avatar">
                                <div class="pull-left mr">
                                   <img :src="user.avatar||'//reservation.cmsone.it/common/images/sb/settimana-bianca-avatar.jpg'" alt="Image" class="media-box-object img-circle thumb48">
                                </div>
                                <div class="avatar-data">
                                    <div class="text-bold text-sm">{{user.userName}} {{user.userSurname}}</div>
                                    <div class="ucase text-xxs mb-sm ">{{$t( 'user code' )}} <span class="text-orange">{{user.userCode}}</span></div>
                                    <div class="text-xxs">{{$t( 'user since' )}} {{user.userSince | DMY}}</div>
                                </div>
                            </div>
                        `,
                    filters:{
                ...allFilters
                },
                props:['user']
            },
            UserCashBack,
            ReservationRecap:{
                template:`<div class="text-center">
                            <div class="text-xxs mb-xxs">
                                <div>{{$t('reservation made' , {count:user.reservationCount}, user.reservationCount )}}</div>
                                <div class="text-bold" v-if="user.reservationCount>0">{{$t('last booking made')}} {{user.lastBookingDate | DMY}} </div>
                            </div>
                            <div>
                                 <button type="button" class="btn btn-primary btn-xs text-xxs wd-wide p-xs">
                                     {{ $t('user profile')}}  <span aria-hidden="true" class="text-bold">&rsaquo;</span>
                                 </button>
                            </div>
                        </div>`,
                    filters:{
                ...allFilters
                },
                props:['user']
            }
        },
        methods:{
            onContainerResize(){

            }
        },
        computed:{
            ...mapState(['debugMode'])
        },
        props:{
            user:Object
        }
    }
</script>
<style lang="scss">
    @import "../../../../../assets/scss/app/variables";
    @import "../../../../../assets/scss/app/media-queries";
    @import "../../../../../assets/scss/bootstrap/bootstrap/mixins/grid";
    @import "../../../../../assets/scss/bootstrap/bootstrap/mixins/clearfix";
    @import "../../../../../assets/scss/breakpoint";

    $mq-split-as: 1125px;
    $mq-split-no-as: ($mq-up-to-tablet - 30);
    $aside-bk: max-width $mq-up-to-tablet;
    $split-as: max-width $mq-split-as ;
    $split-no-as: max-width $mq-split-no-as ;
    $mobile: max-width $mq-up-to-mobile ;

    //desktop xl
    .user-avatar-container {  @include make-xs-column(3); }
    .user-cash-back-container {
        @include make-xs-column(6);
        .values{ @include make-xs-column(6); }
    }
    .reservation-recap-container { @include make-xs-column(3); }
    //widgets 2 line with aside visible
    @include breakpoint($split-as) {
        .user-avatar-container { @include make-xs-column(4);  }
        .user-cash-back-container {
            @include make-xs-column(8);
            .values{ @include make-xs-column(6); }
        }
        .reservation-recap-container {  @include make-xs-column(12); }
    }
    //aside not visible
    @include breakpoint($aside-bk) {
        .user-avatar-container { @include make-xs-column(3); }
        .user-cash-back-container {
                @include make-xs-column(6);
                .values { @include make-xs-column(6);  };
        }
        .reservation-recap-container {    @include make-xs-column(3); }
    }
    //aside not visible
    @include breakpoint($split-no-as) {
        .user-avatar-container { @include make-xs-column(4);  }
        .user-cash-back-container { @include make-xs-column(8); }
        .reservation-recap-container { @include make-xs-column(12); }
    }
    // mobile version
    @include breakpoint($mobile) {
        .user-avatar-container { @include make-xs-column(12);  }
        .user-cash-back-container {
            @include make-xs-column(12);
            .values { @include make-xs-column(12);  };
            .display-value{
                .label,.value{
                    font-size:10px !important;;
                }
            }
        }
        .reservation-recap-container {   @include make-xs-column(12);   }

    }

</style>


